"use client";

import React from 'react';
import { TRANSPORTADORAS_CONFIG } from '@/lib/vehicleData';

interface StatsDashboardProps {
  stats: {
    total: number;
    finalizadas: number;
    emTransito: number;
    carregando: number;
    porFC: {
      GR8: number;
      GR9: number;
      XC9: number;
    };
    porTransportadora: Record<string, number>;
    totalPallets: number;
    totalVolume: number;
    percentualFinalizado: number;
  };
}

const StatsDashboard: React.FC<StatsDashboardProps> = ({ stats }) => {
  return (
    <div className="space-y-6">
      {/* Métricas Principais */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {/* Total de Viagens */}
        <div className="bg-white rounded-lg shadow-sm border p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">Total de Viagens</p>
              <p className="text-3xl font-bold text-gray-900">{stats.total}</p>
              <p className="text-xs text-gray-500 mt-1">Todas as rotas ativas</p>
            </div>
            <div className="w-12 h-12 bg-blue-100 rounded-lg flex items-center justify-center">
              <div className="w-6 h-6 bg-blue-600 rounded"></div>
            </div>
          </div>
        </div>

        {/* Viagens Finalizadas */}
        <div className="bg-white rounded-lg shadow-sm border p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">Finalizadas</p>
              <p className="text-3xl font-bold text-green-600">{stats.finalizadas}</p>
              <p className="text-xs text-gray-500 mt-1">{stats.percentualFinalizado}% do total</p>
            </div>
            <div className="w-12 h-12 bg-green-100 rounded-lg flex items-center justify-center">
              <div className="w-6 h-6 bg-green-600 rounded"></div>
            </div>
          </div>
        </div>

        {/* Em Trânsito */}
        <div className="bg-white rounded-lg shadow-sm border p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">Em Trânsito</p>
              <p className="text-3xl font-bold text-blue-600">{stats.emTransito}</p>
              <p className="text-xs text-gray-500 mt-1">Rotas ativas</p>
            </div>
            <div className="w-12 h-12 bg-blue-100 rounded-lg flex items-center justify-center">
              <div className="w-6 h-6 bg-blue-600 rounded"></div>
            </div>
          </div>
        </div>

        {/* Carregando */}
        <div className="bg-white rounded-lg shadow-sm border p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">Carregando</p>
              <p className="text-3xl font-bold text-yellow-600">{stats.carregando}</p>
              <p className="text-xs text-gray-500 mt-1">Preparação</p>
            </div>
            <div className="w-12 h-12 bg-yellow-100 rounded-lg flex items-center justify-center">
              <div className="w-6 h-6 bg-yellow-600 rounded"></div>
            </div>
          </div>
        </div>
      </div>

      {/* Métricas de Carga */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Total de Pallets */}
        <div className="bg-white rounded-lg shadow-sm border p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">Total de Pallets</p>
              <p className="text-2xl font-bold text-gray-900">{stats.totalPallets.toLocaleString()}</p>
              <p className="text-xs text-gray-500 mt-1">Todas as viagens</p>
            </div>
            <div className="w-10 h-10 bg-purple-100 rounded-lg flex items-center justify-center">
              <div className="w-5 h-5 bg-purple-600 rounded"></div>
            </div>
          </div>
        </div>

        {/* Total de Volume */}
        <div className="bg-white rounded-lg shadow-sm border p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">Volume Total</p>
              <p className="text-2xl font-bold text-gray-900">{stats.totalVolume.toLocaleString()}</p>
              <p className="text-xs text-gray-500 mt-1">Unidades transportadas</p>
            </div>
            <div className="w-10 h-10 bg-indigo-100 rounded-lg flex items-center justify-center">
              <div className="w-5 h-5 bg-indigo-600 rounded"></div>
            </div>
          </div>
        </div>
      </div>

      {/* Distribuição por FC e Transportadoras */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Distribuição por FC */}
        <div className="bg-white rounded-lg shadow-sm border p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">
            Distribuição por Fulfillment Center
          </h3>
          <div className="space-y-4">
            {Object.entries(stats.porFC).map(([fc, count]) => {
              const percentage = stats.total > 0 ? Math.round((count / stats.total) * 100) : 0;
              return (
                <div key={fc} className="flex items-center justify-between">
                  <div className="flex items-center space-x-3">
                    <div className={`w-4 h-4 rounded ${
                      fc === 'GR8' ? 'bg-blue-500' :
                      fc === 'GR9' ? 'bg-green-500' : 'bg-purple-500'
                    }`}></div>
                    <span className="text-sm font-medium text-gray-900">{fc}</span>
                  </div>
                  <div className="flex items-center space-x-2">
                    <span className="text-sm text-gray-600">{count} viagens</span>
                    <span className="text-xs text-gray-500">({percentage}%)</span>
                  </div>
                </div>
              );
            })}
          </div>
        </div>

        {/* Distribuição por Transportadora */}
        <div className="bg-white rounded-lg shadow-sm border p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">
            Viagens por Transportadora
          </h3>
          <div className="space-y-3">
            {Object.entries(stats.porTransportadora)
              .sort(([,a], [,b]) => b - a)
              .map(([transportadora, count]) => {
                const config = TRANSPORTADORAS_CONFIG[transportadora as keyof typeof TRANSPORTADORAS_CONFIG];
                const percentage = stats.total > 0 ? Math.round((count / stats.total) * 100) : 0;
                
                return (
                  <div key={transportadora} className="flex items-center justify-between">
                    <div className="flex items-center space-x-3">
                      <div 
                        className="w-3 h-3 rounded-full"
                        style={{ backgroundColor: config?.cor || '#6B7280' }}
                      ></div>
                      <span className="text-sm font-medium text-gray-900 truncate">
                        {transportadora}
                      </span>
                    </div>
                    <div className="flex items-center space-x-2">
                      <span className="text-sm text-gray-600">{count}</span>
                      <span className="text-xs text-gray-500">({percentage}%)</span>
                    </div>
                  </div>
                );
              })}
          </div>
        </div>
      </div>

      {/* Cobertura das Transportadoras */}
      <div className="bg-white rounded-lg shadow-sm border p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-6">
          Cobertura das Transportadoras por FC
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
          {Object.entries(TRANSPORTADORAS_CONFIG).map(([key, config]) => (
            <div key={key} className="border rounded-lg p-4 hover:shadow-md transition-shadow">
              <div className="flex items-center space-x-2 mb-3">
                <div 
                  className="w-3 h-3 rounded-full"
                  style={{ backgroundColor: config.cor }}
                ></div>
                <h4 className="font-medium text-gray-900 text-sm">{config.nome}</h4>
              </div>
              <div className="space-y-2">
                <p className="text-xs text-gray-600 mb-2">Atende FC:</p>
                <div className="flex flex-wrap gap-1">
                  {['GR8', 'GR9', 'XC9'].map(fc => {
                    const atende = config.fcsAtendidos.includes(fc);
                    return (
                      <span 
                        key={fc}
                        className={`text-xs px-2 py-1 rounded-full font-medium ${
                          atende 
                            ? 'bg-green-100 text-green-800 border border-green-200'
                            : 'bg-red-100 text-red-800 border border-red-200'
                        }`}
                      >
                        {fc} {atende ? '✓' : '✗'}
                      </span>
                    );
                  })}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default StatsDashboard;
