"use client";

import React, { useState, useEffect, useCallback } from 'react';
import { 
  VehicleTrackingData, 
  fetchVehicleDataFromExcel, 
  filterByFC, 
  filterByTransportadora, 
  filterByStatus,
  calculateStatistics 
} from '@/lib/vehicleData';
import VehicleTable from '@/components/VehicleTable';
import StatsDashboard from '@/components/StatsDashboard';
import FilterControls from '@/components/FilterControls';

export default function HomePage() {
  // Estados principais
  const [allData, setAllData] = useState<VehicleTrackingData[]>([]);
  const [filteredData, setFilteredData] = useState<VehicleTrackingData[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [lastUpdate, setLastUpdate] = useState<Date | null>(null);

  // Estados dos filtros
  const [selectedFC, setSelectedFC] = useState<string>('');
  const [selectedTransportadora, setSelectedTransportadora] = useState<string>('');
  const [selectedStatus, setSelectedStatus] = useState<string>('');

  // Função para carregar dados
  const loadData = useCallback(async () => {
    setLoading(true);
    setError(null);
    
    try {
      const data = await fetchVehicleDataFromExcel();
      setAllData(data);
      setLastUpdate(new Date());
    } catch (err) {
      setError('Erro ao carregar dados da planilha Excel. Verifique a conexão.');
      console.error('Erro ao carregar dados:', err);
    } finally {
      setLoading(false);
    }
  }, []);

  // Efeito para carregar dados iniciais
  useEffect(() => {
    loadData();
  }, [loadData]);

  // Efeito para aplicar filtros
  useEffect(() => {
    let filtered = allData;
    
    if (selectedFC) {
      filtered = filterByFC(filtered, selectedFC);
    }
    
    if (selectedTransportadora) {
      filtered = filterByTransportadora(filtered, selectedTransportadora);
    }
    
    if (selectedStatus) {
      filtered = filterByStatus(filtered, selectedStatus);
    }
    
    setFilteredData(filtered);
  }, [allData, selectedFC, selectedTransportadora, selectedStatus]);

  // Handlers dos filtros
  const handleFCChange = (fc: string) => setSelectedFC(fc);
  const handleTransportadoraChange = (transportadora: string) => setSelectedTransportadora(transportadora);
  const handleStatusChange = (status: string) => setSelectedStatus(status);
  
  const handleClearFilters = () => {
    setSelectedFC('');
    setSelectedTransportadora('');
    setSelectedStatus('');
  };

  // Calcular estatísticas
  const statistics = calculateStatistics(filteredData);

  return (
    <div className="space-y-8">
      {/* Cabeçalho da Página */}
      <div className="bg-gradient-to-r from-blue-600 to-blue-800 rounded-lg shadow-lg text-white p-8">
        <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between">
          <div>
            <h1 className="text-3xl font-bold mb-2">
              Rastreamento de Veículos Amazon
            </h1>
            <p className="text-blue-100 text-lg mb-4">
              Monitoramento completo das rotas TZX → Fulfillment Centers
            </p>
            <div className="flex flex-wrap gap-4 text-sm">
              <div className="flex items-center space-x-2">
                <div className="w-2 h-2 bg-white rounded-full"></div>
                <span>Origem: TZX</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-2 h-2 bg-white rounded-full"></div>
                <span>Destinos: GR8, GR9, XC9</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-2 h-2 bg-white rounded-full"></div>
                <span>7 Transportadoras Ativas</span>
              </div>
            </div>
          </div>
          
          <div className="mt-6 lg:mt-0 lg:text-right">
            <div className="bg-white/10 backdrop-blur rounded-lg p-4">
              <div className="text-sm text-blue-100 mb-1">Planilha Excel Online</div>
              <div className="font-semibold">TRANSFERÊNCIA PCP - 2025</div>
              {lastUpdate && (
                <div className="text-xs text-blue-200 mt-2">
                  Última atualização: {lastUpdate.toLocaleString('pt-BR')}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Mensagem de Erro */}
      {error && (
        <div className="bg-yellow-50 border-l-4 border-yellow-400 p-4 rounded-lg">
          <div className="flex">
            <div className="flex-shrink-0">
              <div className="w-5 h-5 bg-yellow-400 rounded-full"></div>
            </div>
            <div className="ml-3">
              <p className="text-sm text-yellow-800">
                <strong>Aviso:</strong> {error}
              </p>
              <p className="text-xs text-yellow-700 mt-1">
                Exibindo dados simulados baseados na estrutura da planilha real.
              </p>
            </div>
          </div>
        </div>
      )}

      {/* Dashboard de Estatísticas */}
      <StatsDashboard stats={statistics} />

      {/* Controles de Filtro */}
      <FilterControls
        selectedFC={selectedFC}
        selectedTransportadora={selectedTransportadora}
        selectedStatus={selectedStatus}
        onFCChange={handleFCChange}
        onTransportadoraChange={handleTransportadoraChange}
        onStatusChange={handleStatusChange}
        onClearFilters={handleClearFilters}
        totalRecords={allData.length}
        filteredRecords={filteredData.length}
        onRefresh={loadData}
        isLoading={loading}
      />

      {/* Informações de Conexão com Excel */}
      <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
        <div className="flex items-start space-x-3">
          <div className="flex-shrink-0">
            <div className="w-5 h-5 bg-blue-500 rounded-full flex items-center justify-center">
              <div className="w-2 h-2 bg-white rounded-full"></div>
            </div>
          </div>
          <div className="flex-1">
            <h3 className="text-sm font-medium text-blue-900 mb-1">
              Integração com Excel Online
            </h3>
            <p className="text-sm text-blue-700">
              Os dados são sincronizados automaticamente com a planilha "TRANSFERÊNCIA PCP - 2025" 
              hospedada no SharePoint da Total Express. A estrutura inclui todas as colunas originais: 
              ISA, Data da Carga, Rota, Tipo de Veículo, horários planejados, ID Viagem, SM, CAF, 
              Pallets, Volume, horários reais, status de finalização e informações da transportadora.
            </p>
            <div className="mt-2 text-xs text-blue-600">
              URL: texcourierltda-my.sharepoint.com → TRANSFERÊNCIA PCP - 2025.xlsx
            </div>
          </div>
        </div>
      </div>

      {/* Tabela Principal */}
      <VehicleTable data={filteredData} loading={loading} />

      {/* Resumo Final */}
      {!loading && filteredData.length > 0 && (
        <div className="bg-gray-50 rounded-lg p-6">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 text-center">
            <div>
              <div className="text-2xl font-bold text-gray-900">
                {statistics.total}
              </div>
              <div className="text-sm text-gray-600">Total de Viagens</div>
            </div>
            <div>
              <div className="text-2xl font-bold text-green-600">
                {statistics.percentualFinalizado}%
              </div>
              <div className="text-sm text-gray-600">Taxa de Finalização</div>
            </div>
            <div>
              <div className="text-2xl font-bold text-blue-600">
                {statistics.totalPallets.toLocaleString()}
              </div>
              <div className="text-sm text-gray-600">Pallets Transportados</div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
