// Interface para os dados da planilha Excel Amazon
export interface VehicleTrackingData {
  isa: string;
  dataCarga: string;
  rota: string;
  tipoVeiculo: string;
  planejadoChegadaTZX: string;
  planejadoSaidaTZX: string;
  planejadoChegadaFC: string;
  idViagem: string;
  sm: string;
  caf: string;
  pallets: number;
  vol: number;
  chegadaOrigem: string;
  saidaOrigem: string;
  chegadaDestino: string;
  finalizado: boolean;
  baixaCAF: boolean;
  transportadora: string;
  motorista: string;
  placa: string;
  status: 'Em Trânsito' | 'Carregando' | 'Finalizado' | 'Aguardando';
}

// Configuração das transportadoras e suas coberturas
