"use client";

import React from 'react';
import { TRANSPORTADORAS_CONFIG, FCS_DISPONIVEIS } from '@/lib/vehicleData';

interface FilterControlsProps {
  selectedFC: string;
  selectedTransportadora: string;
  selectedStatus: string;
  onFCChange: (fc: string) => void;
  onTransportadoraChange: (transportadora: string) => void;
  onStatusChange: (status: string) => void;
  onClearFilters: () => void;
  totalRecords: number;
  filteredRecords: number;
  onRefresh: () => void;
  isLoading: boolean;
}

const FilterControls: React.FC<FilterControlsProps> = ({
  selectedFC,
  selectedTransportadora,
  selectedStatus,
  onFCChange,
  onTransportadoraChange,
  onStatusChange,
  onClearFilters,
  totalRecords,
  filteredRecords,
  onRefresh,
  isLoading
}) => {
  const hasActiveFilters = selectedFC || selectedTransportadora || selectedStatus;
  const statusOptions = ['Em Trânsito', 'Carregando', 'Finalizado', 'Aguardando'];

  return (
    <div className="bg-white rounded-lg shadow-sm border p-6">
      <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-6">
        {/* Filtros */}
        <div className="flex flex-col sm:flex-row gap-4 flex-1">
          {/* Filtro por FC */}
          <div className="min-w-0 flex-1">
            <label htmlFor="fc-filter" className="block text-sm font-medium text-gray-700 mb-2">
              Filtrar por FC:
            </label>
            <select
              id="fc-filter"
              value={selectedFC}
              onChange={(e) => onFCChange(e.target.value)}
              className="w-full border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent bg-white"
            >
              <option value="">Todos os FC</option>
              {FCS_DISPONIVEIS.map(fc => (
                <option key={fc} value={fc}>{fc}</option>
              ))}
            </select>
          </div>

          {/* Filtro por Transportadora */}
          <div className="min-w-0 flex-1">
            <label htmlFor="transportadora-filter" className="block text-sm font-medium text-gray-700 mb-2">
              Filtrar por Transportadora:
            </label>
            <select
              id="transportadora-filter"
              value={selectedTransportadora}
              onChange={(e) => onTransportadoraChange(e.target.value)}
              className="w-full border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent bg-white"
            >
              <option value="">Todas as Transportadoras</option>
              {Object.keys(TRANSPORTADORAS_CONFIG).map(transportadora => (
                <option key={transportadora} value={transportadora}>
                  {transportadora}
                </option>
              ))}
            </select>
          </div>

          {/* Filtro por Status */}
          <div className="min-w-0 flex-1">
            <label htmlFor="status-filter" className="block text-sm font-medium text-gray-700 mb-2">
              Filtrar por Status:
            </label>
            <select
              id="status-filter"
              value={selectedStatus}
              onChange={(e) => onStatusChange(e.target.value)}
              className="w-full border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent bg-white"
            >
              <option value="">Todos os Status</option>
              {statusOptions.map(status => (
                <option key={status} value={status}>{status}</option>
              ))}
            </select>
          </div>
        </div>

        {/* Controles e Informações */}
        <div className="flex flex-col sm:flex-row items-start sm:items-center gap-4">
          {/* Informações de Registros */}
          <div className="text-sm text-gray-600">
            <div className="font-medium">
              Exibindo {filteredRecords.toLocaleString()} de {totalRecords.toLocaleString()} registros
            </div>
            {hasActiveFilters && (
              <div className="text-xs text-gray-500 mt-1">
                Filtros aplicados
              </div>
            )}
          </div>

          {/* Botões de Ação */}
          <div className="flex gap-2">
            {hasActiveFilters && (
              <button
                onClick={onClearFilters}
                className="px-4 py-2 text-sm text-gray-600 hover:text-gray-800 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
              >
                Limpar Filtros
              </button>
            )}
            
            <button
              onClick={onRefresh}
              disabled={isLoading}
              className="px-4 py-2 text-sm bg-blue-600 hover:bg-blue-700 disabled:bg-blue-400 text-white rounded-lg font-medium transition-colors flex items-center gap-2"
            >
              {isLoading ? (
                <>
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                  Atualizando...
                </>
              ) : (
                'Atualizar Dados'
              )}
            </button>
          </div>
        </div>
      </div>

      {/* Filtros Ativos */}
      {hasActiveFilters && (
        <div className="mt-4 pt-4 border-t border-gray-200">
          <div className="flex flex-wrap items-center gap-2">
            <span className="text-sm text-gray-600">Filtros ativos:</span>
            
            {selectedFC && (
              <span className="inline-flex items-center gap-1 px-3 py-1 bg-blue-100 text-blue-800 text-sm rounded-full">
                FC: {selectedFC}
                <button
                  onClick={() => onFCChange('')}
                  className="ml-1 hover:bg-blue-200 rounded-full p-0.5"
                >
                  <div className="w-3 h-3 text-blue-600">×</div>
                </button>
              </span>
            )}
            
            {selectedTransportadora && (
              <span className="inline-flex items-center gap-1 px-3 py-1 bg-green-100 text-green-800 text-sm rounded-full">
                {selectedTransportadora}
                <button
                  onClick={() => onTransportadoraChange('')}
                  className="ml-1 hover:bg-green-200 rounded-full p-0.5"
                >
                  <div className="w-3 h-3 text-green-600">×</div>
                </button>
              </span>
            )}
            
            {selectedStatus && (
              <span className="inline-flex items-center gap-1 px-3 py-1 bg-purple-100 text-purple-800 text-sm rounded-full">
                Status: {selectedStatus}
                <button
                  onClick={() => onStatusChange('')}
                  className="ml-1 hover:bg-purple-200 rounded-full p-0.5"
                >
                  <div className="w-3 h-3 text-purple-600">×</div>
                </button>
              </span>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default FilterControls;
